.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.degreePlansList {
    min-height: 50vh;
    max-width: 65vh;
    flex: 1;
}

.degreePlan {
    width: 100%;
    align-items: center;
    /* display: flex; */
    flex: 4;
}

.semester {
    /* width: 80%; */
    /* align-items: center;
    justify-content: center; */
    /* background-color: #3c9fdc; */
    display: flex;
    flex-direction: column;
}

.intro {
    min-height: 50vh;
    font-size: calc(7px + 2vmin);
}

.intro-header {
    font-size: calc(10px + 2vmin);
}

.App-header {
    width: 100%;
    background-color: #282c34;
    min-height: 10vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
